	module.exports = {"data":{"page":{"id":"/Users/Isaac/website/content/pages/1--about/index.md absPath of file >>> MarkdownRemark","html":"<p>Senior at the University of Utah studying mathematics and (sometimes) physics. I am currently most interested in algebraic geometry and commutative algebra, but hope to learn more number theory in the near future.</p>\n<p>Next year, I will attend Part III in maths at the <a href=\"https://www.maths.cam.ac.uk/postgrad/part-iii/prospective.html\">University of Cambridge</a> with funding from the <a href=\"https://churchillscholarship.org/\">Churchill Scholarship</a>. At Utah, I am completing my honors thesis in mathematics under Anurag Singh, and have previously benefited from the mentorship of Karl Schwede and Thomas Polstra.</p>\n<p>I also owe thanks to the excellent Zhirayr Avetisyan, who mentored me during the summer of 2019 at UC Santa Barbara; Ignacio Darago, who mentored me during the summer of 2020 at the University of Chicago; and Adam Boocher, who initially set me down my path in mathematics and continues to offer friendly advice and help many years later. During my early years at the University of Utah, I was lucky to briefly work in Dr. Vikram Deshpande and Dr. Yue Zhao’s research groups.</p>\n<p>I am fortunate to have received funding from the <a href=\"https://honors.utah.edu/eccles-scholarship/\">Eccles scholarship</a> during my four years at the University of Utah, as well as from the <a href=\"https://goldwater.scholarsapply.org/\">Goldwater scholarship</a>.</p>","frontmatter":{"title":"About"}},"footnote":{"id":"/Users/Isaac/website/content/parts/footnote.md absPath of file >>> MarkdownRemark","html":"<ul>\n<li>this site is modified from the <a href=\"https://github.com/greglobinski/gatsby-starter-personal-blog\">gatsby-starter-personal-blog</a></li>\n</ul>"},"site":{"siteMetadata":{"facebook":{"appId":"..."}}}},"pathContext":{"slug":"/about/"}}

